/**
 * native 
 * window.WebViewJavascriptBridge = {
 *  `registerHandler, //  native 
 *  callHandler, //  native   handlerName 
 *  disableJavscriptAlertBoxSafetyTimeout, //  setTimeout 
 *  _fetchQueue, // native  js 
 *  _handleMessageFromObjC // native  js callback
 *  }`
 */

import Browser from './utils/browser'

export let ready = callback => {
  if (window.WebViewJavascriptBridge) {
    return callback(window.WebViewJavascriptBridge)
  }
  if (window.WVJBCallbacks) {
    return window.WVJBCallbacks.push(callback)
  }
  window.WVJBCallbacks = [callback]
  const WVJBIframe = document.createElement('iframe')
  WVJBIframe.style.display = 'none'
  if (Browser.isAndroid) {
    WVJBIframe.src = 'androidHttps://__bridge_loaded__'
  } else {
    WVJBIframe.src = 'https://__bridge_loaded__'
  }
  document.documentElement.appendChild(WVJBIframe)
  setTimeout(() => document.documentElement.removeChild(WVJBIframe), 0)
}

export default {
  ready,
}
